import React from "react";
import { Link } from "react-router-dom";
// import dryfish from "../images/dryfish-1.jpg";
// import ponmo from "../images/ponmo-1.jpg";
// import ponmo2 from "../images/ponmo-3.jpg";
// import { Fade } from "react-reveal";
import charis from "../images/charis-logo.JPG";
function Home() {
  return (
    <div className="contain">
      <center>
        <div className="body">
          <div>
            <p
              className="moving"
              style={{
                fontWeight: "bold",
                fontSize: "10px",
                color: "green",
                textTransform: "uppercase",
              }}
            >
              No.1 Platform to order your raw foods!
            </p>
          </div>
          <h1
            className="head"
            style={{
              fontWeight: "",
              fontSize: "23px",
              textTransform: "uppercase",
              marginTop: "10px",
            }}
          >
            {/* Welcome to{" "} */}
            <span
              className="head"
              style={{ color: "green", fontFamily: "", fontWeight: "bolder" }}
            >
              Charis
            </span>{" "}
            Ventures
          </h1>

          <span
            className="good"
            style={{
              textTransform: "",
              fontSize: "10px",
              color: "crimson",
              fontWeight: "bolder",
              fontStyle: "normal",
              position: "relative",
              bottom: "25px",
              fontWeight: "bold",
            }}
          >
            We sell raw foods, Dried Catfish,Fried white Ponmo,and Ponmo ijebu.
          </span>
          <br />
          <br />
          <div className="ponmo">
            <br />
            <img
              src={charis}
              style={{ width: "70%", position: "relative", bottom: "60px" }}
            />
          </div>
        </div>
      </center>

      <div className="mini-body" style={{ marginTop: "20px" }}>
        <p
          style={{ fontSize: "15px", fontFamily: "serif", fontStyle: "italic" }}
        >
          <span>
            <i
              style={{
                marginLeft: "20px",
                color: "crimson",
                marginRight: "15px",
              }}
              className="fa fa-clock"
            ></i>
          </span>
          10:00AM - 9:00PM
        </p>
        <p
          style={{
            fontSize: "15px",
            marginLeft: "20px",
            fontFamily: "serif",
            fontStyle: "italic",
          }}
        >
          <span>
            <i
              style={{ marginRight: "15px", color: "crimson" }}
              className="fa fa-map-marker-alt"
            ></i>
          </span>
          Ibadan and Osogbo
        </p>
        <p
          style={{
            fontSize: "15px",
            marginLeft: "20px",
            fontFamily: "serif",
            fontStyle: "italic",
            textTransform: "capitalize",
          }}
        >
          <span>
            <i
              style={{ marginRight: "15px", color: "crimson" }}
              className="fa fa-biking"
            ></i>
          </span>
          24hours-3working days
        </p>
      </div>
      <center>
        <div className="btns">
          <Link to="/goods">
            <button
              className="buttons"
              style={{
                fontSize: "12px",
                textTransform: "capitalize",
                fontStyle: "italic",
              }}
            >
              <i className="fa fa-gift"></i> click here to check available
              products
            </button>
          </Link>
        </div>
        <div className="mini-footer">
          <h1
            style={{
              color: "crimson",
              textTransform: "capitalize",
              position: "relative",
              bottom: "-20px",
              fontSize: "8.5px",
              fontWeight: "bolder",
            }}
          >
            copyright 2024 Charis Ventures | all rights reserved.
          </h1>
        </div>
      </center>
    </div>
  );
}

export default Home;
