import React, { Component } from "react";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from "react-responsive-carousel";
import ceo from "../images/ceo.JPG";
import Tosin from "../images/tosin.JPG";
import Family from "../images/family.JPG";
import debby from "../images/debby.JPG";

export default class Testimonials extends Component {
  render() {
    return (
      <Carousel
        showArrows={true}
        infiniteLoop={true}
        showThumbs={false}
        showStatus={false}
        autoPlay={true}
        interval={6100}
      >
        <div style={{ background: "crimson" }}>
          <div className="myCarousel">
            <h3 style={{ fontWeight: "bolder" }}>Kayode Tobiloba</h3>
            <p style={{ color: "green" }}>
              Awesome place to be for all your foodstuff"
            </p>
          </div>
        </div>
        <div style={{ background: "crimson" }}>
          <img src={Family} />
          <div className="myCarousel">
            <h3 style={{ fontWeight: "bolder" }}>The Olatunde's</h3>
            <p style={{ color: "green" }}>
              I discovered Charis Ventures yesterday. I ordered my dried fish
              and ponmo in the midnight. They delivered to my doorstep this
              morning. Bye Bye to Bodija and its cruel sun"
            </p>
          </div>
        </div>

        <div style={{ background: "crimson" }}>
          <img src={debby} />
          <div className="myCarousel">
            <h3 style={{ fontWeight: "bolder" }}>Ojo Timileyin</h3>
            <p style={{ color: "green" }}>
              I really enjoy your services. You guys are the best thing to
              happen to Ibadan and its environments. More power to your elbows.
            </p>
          </div>
        </div>
      </Carousel>
    );
  }
}
